import Post from "./postTemplate"
import img1 from "../_img/post2Img/post2_1.jpg"
import img2 from "../_img/post2Img/post2_2.jpg"
import vid1 from "../_img/post2Img/post2_3.mp4"
import img3 from "../_img/post2Img/post2_4.jpg"
import img4 from "../_img/post2Img/post2_5.jpg"
import img5 from "../_img/post2Img/post2_6.jpg"
import img6 from "../_img/post2Img/post2_7.jpg"
import img7 from "../_img/post2Img/post2_8.jpg"
import img8 from "../_img/post2Img/post2_9.jpg"
import img9 from "../_img/post2Img/post2_10.jpg"
import vid2 from "../_img/post2Img/post2_11.mp4"
import vid3 from "../_img/post2Img/post2_12.mp4"
import img10 from "../_img/post2Img/post2_13.jpg"
import img11 from "../_img/post2Img/post2_14.jpg"
import img12 from "../_img/post2Img/post2_15.jpg"
import img13 from "../_img/post2Img/post2_16.jpg"
import img14 from "../_img/post2Img/post2_17.jpg"
import img15 from "../_img/post2Img/post2_18.jpg"
import img16 from "../_img/post2Img/post2_19.jpg"
import img17 from "../_img/post2Img/post2_20.jpg"
import img18 from "../_img/post2Img/post2_21.jpg"
import img19 from "../_img/post2Img/post2_22.jpg"
import img20 from "../_img/post2Img/post2_23.jpg"
import img21 from "../_img/post2Img/post2_24.jpg"
import img22 from "../_img/post2Img/post2_25.jpg"
import img23 from "../_img/post2Img/post2_26.jpg"
import img24 from "../_img/post2Img/post2_27.jpg"
import ImageComponent from "../component/ImageComponent/ImageComponent"

let post_title = "Le paradis sur terre : l'île des Pins"
let post_text =
    <div>
        <div className = 'FullPostPicture'>
            <p>
                L'île des Pins est une île située tout au sud de la Nouvelle-Calédonie. Pourquoi "Îles des Pins" ? C'est dû au <strong>pins colonaires</strong>, assez caractéristiques de l'île même s'ils se retrouvent ailleurs sur le téritoire calédonien. Il y a environ 3 000 habitants sur l'île.
            </p>
            <ImageComponent img = { img1 } alt = { "Pins colonaires" } description = { "Exemple de Pin Colonaire. Il y en a d'autres sur les prochains photos, savez-vous les retrouver ? ;)" } />
            <p>
                Après une petite journée de repos à Nouméa où l'on a récupéré comme on a pu des 10h de décalage horaire, nous voilà reparti en avion. La compagnie est <i>Air Calédonie</i> qui permet de faire les liaisons intérieures au sein de la Calédonie (notamment entre les îles). 
                Les pistes sont assez courtes, les décollages et atterrissages sont assez secs.
                Premier vol où la moitié des passagers sont en tong. Et à vrai dire, c'est plutôt agréable.
            </p>
            <ImageComponent img = { img2 } alt = { "Avion Air Calédonie" } description = { "L'aéroport de Nouméa Magenta" } />
            <p>
                Nous survolons des paysages incroyables lorsque nous arrivons à l'îles des pins. Le premier aperçu de la Nouvelle-Calédonie est magique.
            </p>
            <video controls>
                <source src = { vid1 } />
            </video>
            <p>
                A l'aéroport de l'île des pins, les bagages sont récupérés dehors à un comptoir: très très loin des tapis roulants interminables des aéroports visités les jours précédents.
            </p>
            <p>
                Il fait chaud, crème solaire obligatoire sinon crâmage instantané ! D'ailleurs, il y avait un incendie en bout de piste lorsque nous avons atterri, plus de 60 hectares de fôrets sont partis en fumée la veille.
            </p>
            <p>
                Nous arrivons au camping Nataiwatch et à la baie de Kanuméra.
            </p>
            <ImageComponent img = { img3 } alt = { "Kanuméra" } description = { "Baie de Kanuméra. Indice pour les pins colonaires" } />
            <ImageComponent img = { img4 } alt = { "" } description = { "Jeux dans l'eau" } />
            <ImageComponent img = { img5 } alt = { "" } description = { "Objectif coco" } />
            <ImageComponent img = { img6 } alt = { "" } description = { "" } />
            <p>
                <strong>Sortie pirogue dans la baie d'Upi et la baie d'Oro</strong>
            </p>
            <ImageComponent img = { img7 } alt = { "" } description = { "Le banian cathédral" } />
            <ImageComponent img = { img8 } alt = { "" } description = { "La piscine naturelle d'Oro" } />
            <p>
                Ce spot a été l'un des coups de coeur pour le groupe. Malgré le monde (on ne se marchait pas dessus non plus, loin de là), la séance PMT a été incroyable avec des dizaines et des dizaines de poissons et des oursins énormes. Incroyable.
            </p>
            <video width = "70%" margin = "auto" controls>
                <source src = { vid2 } />
            </video>
            <p>
                Petit jeu sur la plage
            </p>
            <video width = "100%" controls>
                <source src = { vid3 } />
            </video>
            <p>
                <strong>Sortie canoë dans la baie de Gadji</strong>
            </p>
            <p>
                Il faut savoir que ceci a été un plan B improvisé sur le tas suite à la pénurie d'essence sur l'île qui nous a empêché de faire une sortie PMT sur l'île aux brosses (les pins colonaires donnent à l'île une forme de brosse).
                <br/>
                Nous avons été acceuilli par Nicole qui habite dans une vanilleraie. Trop gentille.
                <br/>
                La baie de Gadji a aussi acceuilli l'émission de télévision <i>Koh Lanta</i> en 2005, c'est dire qu'il y a personne dans les environs. Nous ne croiserons d'ailleurs personne pendant toute notre excursion.
            </p>
            <ImageComponent img = { img10 } alt = { "" } description = { "La vanilleraie de Nicole" } />
            <ImageComponent img = { img11 } alt = { "" } description = { "La baie de Gadji avec ses petits îlots qui regorgent de vie (notamment des crabes)" } />
            <ImageComponent img = { img12 } alt = { "" } description = { "" } />
            <ImageComponent img = { img13 } alt = { "" } description = { "Les tâches noires dans l'eau sont les reflets des coraux, il ne faut pas mettre le pied dessus car ça fait mal (surtout quand on tombe du canoë... oups)" } />
            <ImageComponent img = { img14 } alt = { "" } description = { "Une végétation très dense sur les rivages des îlots" } />
            <ImageComponent img = { img15 } alt = { "" } description = { "Seuls sur notre île, il n'y a rien. Nous trouverons une autre île où une table a été installée pour symboliser l'endroit où Koh-Lanta s'est déroulé." } />
            <ImageComponent img = { img16 } alt = { "" } description = { "Petit spot de PMT où nous trouverons notamment des étoiles de mer bleu électriques. Génial !!" } />
            <p>
                <strong>La montée du Pic N'Ga pour aller voir le levé de soleil</strong>
            </p>
            <p>
                Je pars seul à 3h du matin avec la lampe frontale pour aller au sommet du Pic N'Ga, point culminant de l'île des pins (262 mètres). La montée est impressionnate, je ne vois pas à 5 mètres devant moi et je ne connais pas le chemin. J'arrive vers 4h30 en haut, les premières lueurs apparaissent, je suis seul et c'est magique.
            </p>
            <ImageComponent img = { img17 } alt = { "" } description = { "Point de vue sur la baie d'Upi et d'Oro où nous étions l'avant-veille" } />
            <ImageComponent img = { img18 } alt = { "" } description = { "La baie de Kanuméra" } />
            <ImageComponent img = { img19 } alt = { "" } description = { "La croix symbolisant le haut du Pic N'Ga" } />
            <p>
                <strong>Merci à Tchakyo et à son père</strong>
            </p>
            <p>
                Alors que je suis levé à 4h du matin pour aller voir le levé de soleil sur la plage, je rencontre un local et celui-ci est en train d'attendre le bon moment pour pêcher. Il m'explique ses techniques de pêches au filet. la marée était un peu trop basse pour lui permettre d'aller pêcher ce jour là. Il ne fallait surtout pas parler trop fort pour ne pas effrayer les poissons. Aussi, les mouettes mettent en difficulté les pêcheurs. Elles préviennent les poissons de notre présence. Tout un art la pêche et j'aurai adoré partager ce moment avec lui.
            </p>
            <p>
                Alors que je suis parti me balader, le local m'envoie son fils qui me fait découvrir plein de choses. Il est adorable et le courant est très bien passé. Il attendait notre retour tous les soirs pour jouer avec nous. Il s'appelle Tchakyo.
            </p>
            <ImageComponent img = { img20 } alt = { "" } description = { "La baie de Kanuméra" } />
            <ImageComponent img = { img21 } alt = { "" } description = { "" } />
            <ImageComponent img = { img22 } alt = { "" } description = { "La baie de Kanuméra" } />
            <ImageComponent img = { img23 } alt = { "" } description = { "Tient tient..." } />
            <ImageComponent img = { img24 } alt = { "" } description = { "Trouver le serpent (le tricot rayé)" } />
            <p>
                <strong>Retour à Nouméa</strong>
            </p>
            <ImageComponent img = { img9 } alt = { "" } description = { "Petite spécialité à l'aéroport : les nems..." } />
            <p>
                L'île des pins est un grand coup de coeur !
            </p>
        </div>
    </div>
let post_description = "A l'aventure pour trois jours à l'Île des Pins"
let post_date = "samedi 18 novembre au mardi 21 novembre 2023"
let post_location = "Îles des pins"
let post_category = ["Provinse Sud", "Île des pins"]
let post_picCard = <img src = { img2 } alt = "FIRST SLIDE" />
let post_mapinfo = {
        'center': [-22.6154562, 167.480045],
        'zoom': 11
    }
let post_mapMarkers = [
        {
            'center': [-22.6154562, 167.480045]
        }
    ]
let post2 = new Post(post_title, post_text, post_description, post_date, post_location, post_category, post_picCard, post_mapinfo, post_mapMarkers)

export default post2;